import React, { useState, useRef, useEffect} from "react";
import { Tree } from "primereact/tree";
import { TreeSelect,} from 'primereact/treeselect';
import 'primereact/resources/themes/lara-light-indigo/theme.css';   // theme
import 'primereact/resources/primereact.css';                       // core css
import 'primeicons/primeicons.css';                                 // icons
// import 'primeflex/primeflex.css'; 
import "./index.css"


const FilterHierarchy = (props) => {
  const {convertedHierarchy,
         handleChangeHierarchy} = props
  const [nodes, setNodes] = useState([])
  const [selectedNodeKeys, setSelectedNodeKeys] = useState(null)
    const wrapperRef=useRef(null)
    const [selectedKeys, setSelectedKeys] = useState(null);
  

    const onChangeFn=(e)=>{
      const newArr=[]
      const all=e.value
      Object.keys(all).forEach((e)=>{
        if(all[e].checked){
            newArr.push(e)
        }
      })
      handleChangeHierarchy(newArr)
      setSelectedNodeKeys(e.value)
    }

    useEffect(() => {
      setNodes(convertedHierarchy)
  }, [convertedHierarchy]);

    return (
            <div className="card flex justify-content-center border-0" ref={wrapperRef}>
            <Tree value={nodes} options={nodes} selectionMode="checkbox" selectionKeys={selectedNodeKeys} appendTo={wrapperRef.current}
            onSelectionChange={onChangeFn} className="w-full md:w-30rem" />
            </div> 
    );
}

export default FilterHierarchy;