import React, { useState } from "react";
import FilterHierarchy from "../FilterHierarchy";
import "./index.css";

const FilterSortBar = (props) => {
  let {
    fragrances,
    type,
    convertedHierarchy,
    handleOnApplyFilter,
    handleChangeHierarchy,
    handleFragranceChange,
    handlePriceChange,
    handleGenderChange,
    handleQuantityChange,
    handleButtonActive,
    buttonActive,
    priceRange,
    volumeRange,
    name
  } = props;

  const handleActive = (value) => {
    handleButtonActive(value)
  }


  return (
    <>
      <div className="filterSortBar d-block d-lg-none w-100">
        <div className="row">
          <div className="col-6">
            <div
              className="btn btn-dark w-100 mainBtn"
              data-bs-toggle="offcanvas"
              data-bs-target="#offcanvasBottom"
              aria-controls="offcanvasBottom"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                fill="none"
                id="up-down-arrow"
                className="me-1"
              >
                <path
                  fill="#fff"
                  d="M7.175 4.386a.997.997 0 0 1 .552-.35c.178-.048.367-.048.544 0a.997.997 0 0 1 .555.35L11.7 7.145c.399.381.399 1 0 1.382l-.032.03a1.05 1.05 0 0 1-1.442 0L9.042 7.422V15c0 .552-.446 1-.997 1h-.09a.999.999 0 0 1-.997-1V7.42L5.773 8.558a1.05 1.05 0 0 1-1.442 0l-.032-.031a.949.949 0 0 1 0-1.382l2.876-2.758zm9.65 15.228a.997.997 0 0 1-.552.35 1.067 1.067 0 0 1-.544 0 .996.996 0 0 1-.555-.35L12.3 16.855a.949.949 0 0 1 0-1.382l.032-.03a1.05 1.05 0 0 1 1.442 0l1.185 1.135V9c0-.552.446-1 .997-1h.09c.55 0 .997.448.997 1v7.58l1.185-1.137a1.05 1.05 0 0 1 1.442 0l.032.031c.398.382.398 1 0 1.382l-2.876 2.758z"
                ></path>
              </svg>
              SORT
            </div>
          </div>
          <div className="col-6">
            <div
              className="btn btn-dark w-100 mainBtn"
              data-bs-toggle="offcanvas"
              data-bs-target="#offcanvasFilter"
              aria-controls="offcanvasFilter"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-funnel-fill me-1"
                viewBox="0 0 16 16"
              >
                <path d="M1.5 1.5A.5.5 0 0 1 2 1h12a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.128.334L10 8.692V13.5a.5.5 0 0 1-.342.474l-3 1A.5.5 0 0 1 6 14.5V8.692L1.628 3.834A.5.5 0 0 1 1.5 3.5v-2z" />
              </svg>
              FILTER
            </div>
          </div>
        </div>
      </div>
      <div
        className="offcanvas offcanvas-bottom sortOffCanvas"
        tabIndex="-1"
        id="offcanvasBottom"
        aria-labelledby="offcanvasBottomLabel"
      >
        <div className="offcanvas-header">
          <h5 className="offcanvas-title" id="offcanvasBottomLabel">
            Sort By
          </h5>
          <button
            type="button"
            className="btn-close"
            data-bs-dismiss="offcanvas"
            aria-label="Close"
          ></button>
        </div>
        <div className="offcanvas-body small">
          <div className="d-flex flex-column">
            <div className={`${buttonActive === 0 ? `active` : ``} card py-3 mb-2 text-center fw-bold`} onClick={()=>handleActive(0)} >Relevance</div>
            <div className={`${buttonActive === 1 ? `active` : ``} card py-3 mb-2 text-center fw-bold`} onClick={()=>handleActive(1)}>
              Price - Low to High
            </div>
            <div className={`${buttonActive === 2 ? `active` : ``} card py-3 mb-2 text-center fw-bold`} onClick={()=>handleActive(2)}>
              Price - High to Low
            </div>
          </div>
        </div>
      </div>
      <div
        className="offcanvas offcanvas-bottom sortOffCanvas"
        tabIndex="-1"
        id="offcanvasFilter"
        aria-labelledby="offcanvasFilterLabel"
      >
        <div className="offcanvas-header">
          <h5 className="offcanvas-title" id="offcanvasFilterLabel">
            Filters
          </h5>
          <button
            type="button"
            className="btn-close"
            data-bs-dismiss="offcanvas"
            aria-label="Close"
          ></button>
        </div>
        <div className="offcanvas-body small">
          <div className="container-fluid p-0 mb-3" style={{ minHeight: "auto" }}>
            <div className="row">
              <div className="col-4 py-2 rounded-2" style={{background:"#e0e0e0"}}>
                <div
                  className="nav flex-column nav-pills me-3"
                  id="v-pills-tab"
                  role="tablist"
                  aria-orientation="vertical"
                >
                  {(type == "hierarchy" && convertedHierarchy.length) ? <button
                    className="nav-link active"
                    id="v-pills-home-tab"
                    data-bs-toggle="pill"
                    data-bs-target="#v-pills-home"
                    type="button"
                    role="tab"
                    aria-controls="v-pills-home"
                    aria-selected="true"
                  >
                  {"Categories"}

                  </button> : ""}
                  {(type != "gender") ?
                  <button
                    className="nav-link"
                    id="v-pills-gender-tab"
                    data-bs-toggle="pill"
                    data-bs-target="#v-pills-gender"
                    type="button"
                    role="tab"
                    aria-controls="v-pills-gender"
                    aria-selected="false"
                  >
                    Gender
                  </button>: ""}
        
                  <button
                    className="nav-link"
                    id="v-pills-profile-tab"
                    data-bs-toggle="pill"
                    data-bs-target="#v-pills-profile"
                    type="button"
                    role="tab"
                    aria-controls="v-pills-profile"
                    aria-selected="false"
                  >
                    Volume
                  </button>
                  <button
                    className="nav-link"
                    id="v-pills-disabled-tab"
                    data-bs-toggle="pill"
                    data-bs-target="#v-pills-disabled"
                    type="button"
                    role="tab"
                    aria-controls="v-pills-disabled"
                    aria-selected="false"
                  >
                    Price
                  </button>
                  {fragrances.length ? (      
                  <button
                    className="nav-link"
                    id="v-pills-messages-tab"
                    data-bs-toggle="pill"
                    data-bs-target="#v-pills-messages"
                    type="button"
                    role="tab"
                    aria-controls="v-pills-messages"
                    aria-selected="false"
                  >
                    Fragrance Family
                  </button>) : ""}
                </div>
              </div>
              <div className="col-8">
                <div className="tab-content" id="v-pills-tabContent">
                  {(type == "hierarchy" && convertedHierarchy.length) ? <div
                    className="tab-pane fade show active"
                    id="v-pills-home"
                    role="tabpanel"
                    aria-labelledby="v-pills-home-tab"
                    tabIndex="0"
                  >
                   <FilterHierarchy
                    convertedHierarchy={convertedHierarchy}
                    handleChangeHierarchy={handleChangeHierarchy}
                    />
                  </div> : ""}
                  <div
                    className="tab-pane fade"
                    id="v-pills-profile"
                    role="tabpanel"
                    aria-labelledby="v-pills-profile-tab"
                    tabIndex="0"
                  >
                    {volumeRange.map((item, index) => {
                      return (
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            value=""
                            id={`quantity${index + 1}`}
                            onChange={(value) => handleQuantityChange(item, value.target.checked)}
                          />
                          <label className="form-check-label" for={`quantity${index + 1}`}>
                            {item}ml
                          </label>
                        </div>
                      )
                    })}
                  </div>
                  {type != "gender" ?   
                  <div
                    className="tab-pane fade"
                    id="v-pills-gender"
                    role="tabpanel"
                    aria-labelledby="v-pills-gender-tab"
                    tabIndex="0"
                  >
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        value=""
                        id="gender1"
                        onChange={(value) => handleGenderChange("Men", value.target.checked)}
                      />
                      <label className="form-check-label" for="gender1">
                        Men
                      </label>
                    </div>
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        value=""
                        id="gender2"
                        onChange={(value) => handleGenderChange("Women", value.target.checked)}
                      />
                      <label className="form-check-label" for="gender2">
                        Women
                      </label>
                    </div>
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        value=""
                        id="gender3"
                        onChange={(value) => handleGenderChange("Unisex", value.target.checked)}
                      />
                      <label className="form-check-label" for="gender3">
                        Unisex
                      </label>
                    </div>
                  </div> : ""}
                  <div
                    className="tab-pane fade"
                    id="v-pills-disabled"
                    role="tabpanel"
                    aria-labelledby="v-pills-disabled-tab"
                    tabIndex="0"
                  >
                    {priceRange.map((item, index) => {
                      if(item.length > 1)
                      return (
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            value=""
                            id={`price${index + 1}`}
                            onChange={(value) => handlePriceChange(item, value.target.checked)}
                          />
                          <label className="form-check-label" for={`price${index + 1}`}>
                            &#8377;{item[0]} - &#8377; {(item[1] == 9999) ? "above" : item[1]}
                          </label>
                        </div>
                      )
                      else 
                      return (
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            value=""
                            id={`price${index + 1}`}
                            onChange={(value) => handlePriceChange(item, value.target.checked)}
                          />
                          <label className="form-check-label" for={`price${index + 1}`}>
                            &#8377;{item[0]}
                          </label>
                        </div>
                      ) 
                    })}
                  </div>
                  {fragrances.length ? (   
                  <div
                    className="tab-pane fade"
                    id="v-pills-messages"
                    role="tabpanel"
                    aria-labelledby="v-pills-messages-tab"
                    tabIndex="0"
                  >
                    {fragrances.map((fragrance,index) => {
                      return (
                        <div className="form-check">
                        <input
                          className="form-check-input"
                          type="checkbox"
                          id={`fragrance${index}`}
                          onChange={(value) => handleFragranceChange(fragrance.value, value.target.checked, index)}
                        />
                        <label className="form-check-label" for={`fragrance${index}`}>
                           {fragrance.label}
                        </label>
                      </div>
                      )
                    })}
                  </div>): ""}
                  <div
                    className="tab-pane fade"
                    id="v-pills-settings"
                    role="tabpanel"
                    aria-labelledby="v-pills-settings-tab"
                    tabIndex="0"
                  >
                    ...
                  </div>
                </div>
              </div>
              <button className="btn btn-dark w-75 mx-auto mt-3" data-bs-toggle="offcanvas" data-bs-target="#offcanvasFilter" onClick={handleOnApplyFilter}>Apply Filters</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default FilterSortBar;
